<template>
  <v-container>
    <v-row class="d-flex justify-space-between">
      <v-col class="pb-0">
        <download-excel
          v-if="json_data && json_fields"
          class="btn btn-default"
          :data="json_data"
          :fields="json_fields"
          worksheet="My Worksheet"
          type="csv"
          name="exemplo.csv"
        >
          <v-btn type="submit" block color="primary" tile>
            Export CSV
          </v-btn>
        </download-excel>
      </v-col>
      <v-col class="pb-0">
        <v-autocomplete
            v-model="team"
            :items="teams"
            item-text="fullName"
            item-value="shortName"
            outlined
            dense
            label="Team"
            @change="showPlayers()"
          ></v-autocomplete>
      </v-col>
    </v-row>
    <v-row class="text-center">
      <v-col cols="12">
        <template>
          <v-data-table
            fixed-header
            height="400px"
            dense
            :headers="headers"
            :items="players"
            item-key="id"
            show-expand
            class="elevation-1"
            :loading="loading"
            loading-text="Loading... Please wait"
          >
            <template v-slot:item.photo="{ item }">
              <v-list-item-avatar
                class="mx-auto"
                color="grey"
                size="24"
              >
                <img
                  :src="item.photo ? item.photo : `avatar.png`"
                  :alt="item.name"
                />
              </v-list-item-avatar>
            </template>
            <template v-slot:expanded-item="{ headers, item }">
              <td :colspan="headers.length">
                <span class="px-4"><strong>Residence:</strong> {{ item.residence }}</span>
                <span class="px-4"><strong>Education Level:</strong> {{ item.educationLevel }}</span>
                <span class="px-4"><strong>Dream:</strong> {{ item.dream }}</span>
              </td>
            </template>
          </v-data-table>
        </template>
      </v-col>
    </v-row>
  </v-container>
</template>

<script>
import firebase from "@/firebaseConfig";
const db = firebase.firestore();
  export default {
    name: 'HelloWorld',
    data: () => ({
      collection: '',
      team: '',
      teams: [],
      players: [],
      headers: [
        { text: 'Photo', value: 'photo' },
        { text: 'Full Name', sortable: true, value: 'name' },
        { text: 'Phone', value: 'telephone' },
        { text: 'Date of birth', value: 'birthDate' },
        { text: 'Birthplace', value: 'birthplace' },
        { text: 'Profisso', value: 'profession' },
        { text: 'Height', value: 'height' },
        { text: 'Foot', value: 'foot' },
        { text: 'Position', value: 'position' },
        { text: 'Jersey', value: 'daamisolaNumber' },
        { text: '', value: 'data-table-expand' },
      ],
      loading: false
    }),
    computed: {
      json_fields () {
        return {
          lastName: 'lastName',
          name: 'firstName',
          phone: 'phone',
          birthDate: 'birthDate',
          birthPlace: 'birthPlace',
          residence: 'residence',
          educationLevel: 'educationLevel',
          profession: 'profession',
          dream: 'dream',
          height: 'height',
          foot: 'foot',
          position: 'position',
          jersey: 'jersey',
          photo: 'photo'
        }
      },
      json_data () {
        return this.players.map(p => {
          return { 
            lastName: p.lastName,
            firstName: p.name,
            phone: p.telephone,
            birthDate: p.birthDate,
            birthPlace: p.birthplace,
            residence: p.residence,
            educationLevel: p.educationLevel,
            profession: p.profession,
            dream: p.dream,
            height: p.height,
            foot: p.foot,
            position: p.position,
            jersey: p.daamisolaNumber,
            photo: p.photo
          }
        })
      }
    },
    created () {
      
      // eslint-disable-next-line no-undef
      Fire.$on('leagueChanged', ({currentLeague}) => {
        if (currentLeague) {
          this.collection = currentLeague.list;
          this.teams = currentLeague.teams;
          this.team = currentLeague.teams[5].shortName;
          this.showPlayers()
        }
      });
    },
    methods: {
      showPlayers () {
        this.players = []
        const vm = this;

        vm.loading = !vm.loading;

        db.collection(`${this.collection}`).where("team", "==", this.team)
          .get()
          .then(function (querySnapshot) {
            querySnapshot.forEach(function (doc) {
              vm.players.push({ ...doc.data(), id: doc.id })
            });
          })
          .then(() => {
            vm.loading = !vm.loading;
          })
          .catch(function (error) {
            console.log("Error getting documents: ", error);
          });
      },
    },
    mounted () {
      // this.showPlayers()
    }
  }
</script>
